<template>

<v-app>

  <v-spacer class="mt-4"></v-spacer>

  <v-card class="mx-auto mb-4" max-width="344" elevation="8" v-for="quote in quotes" :key="quote.id">
    <v-list-item three-line>
      <v-list-item-content>
        <div class="overline mb-4">{{ quote.symbol }} ({{ quote.id }})</div>
        <v-list-item-title class="headline mb-1">{{ quote.name }}</v-list-item-title>
        <v-list-item-subtitle>Greyhound divisely hello coldly fonwderfully</v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>
  </v-card>

  <v-spacer class="mb-4"></v-spacer>

</v-app>

  
</template>

<script>
export default {
  name: 'homepage',
  data(){
    return {
      quotes: []
    }
  },
  methods: {
    getQuotes(){
            const endpoint   = "/api/coins";
            fetch(endpoint).then(response => response.json()).then(json => {
                this.quotes = json.data;
            });
        }
  },
  mounted(){
    this.getQuotes();
  }
}
</script>
